.tbl {
  width: 100%;
  box-shadow: 0 1px 10px rgba(0,0,0,0.2);
  border: 1px solid $secondary-color;
  border-radius: 5px;
  border-spacing: 0;
  border-collapse: separate;
  margin-bottom: 1.5rem;
  text-transform: none;
  overflow: hidden;
}

.tbl thead {
  tr {
    background-color: $secondary-color;
    box-shadow: inset 0 1px 1px rgba(255,255,255,0.4);
    text-shadow: 0 -1px 0 rgba(0,0,0,0.6);

    th {
      padding: .5rem .5rem .6rem;
      color: #fff;
      border-left: 1px solid $secondary-color;
      font-family: $secondary-header-font-family;
      font-weight: 500;
      text-align: left;

      &:first-child {
        border-left: none;
      }

      &.tbl-filters{
        text-align: left;
        padding: 0.8rem 0.6rem;

        h3{
          display: inline-block;
          color: #fff;
          font-size: 1.3rem;
          line-height: 1;
          margin: 0;
        }

        hr{
          background: darken($secondary-color, 10%);
          margin: .8rem 0 0 0;
        }
      }

      &.sortable{
        position: relative;
        padding-right: 1rem;
        cursor: pointer;

        &:before,
        &:after{
          content: '';
          width: 0;
          height: 0;
          position: absolute;
          right: 0.3rem;
          opacity: 0.5;
        }

        &:before{
          top: 0.9rem;
          border-left: 0.3rem solid transparent;
          border-right: 0.3rem solid transparent;
          border-bottom: 0.3rem solid #fff;
        }

        &:after{
          bottom: 1rem;
          border-left: 0.3rem solid transparent;
          border-right: 0.3rem solid transparent;
          border-top: 0.3rem solid #fff;
        }

        &.asc{
          &:before{ top: 0; bottom: 0; margin: auto; }
          &:after{ display: none; }
        }
        &.desc{
          &:before{ display: none; }
          &:after{ top: 0; bottom: 0; margin: auto; }
        }
      }
    }
  }
}

.tbl tbody {
  border-top: none;
  border: none;
  background: white;

  tr{

    &:nth-child(even) {
      background: transparentize($secondary-color, .9);
    }

    td {
      padding: 5px;
      font-size: 14px;
      border-left: 1px solid $secondary-color;

      &:first-child {
        border-left: none;
      }
    }
  }
}

/*
  STATUS
*/

.tbl tbody td.status {
  text-align: center;

  .status-icon {
    display: block;
    width: 24px;
    height: 24px;
    border-radius: 12px;
    margin: 0 auto;
  }

  &.status-ok{
    .status-icon {
      background: #52A300;

      &:before{
        content: '\10004';
        color: #fff;
      }
    }
  }
  &.status-glitch{
    .status-icon {
      background: #CC6600;
    }
  }
  &.status-degradation{
    .status-icon {
      background: #CC0000;

      &:before{
        content: '\10008';
        color: #fff;
      }
    }
  }
}

/*
  DETAILS
*/
.tbl tbody td .details {
  visibility: hidden;
  position: absolute;
  opacity: 0;
  top: 100%;
  left: 0%;
  width: 280px;
  z-index: 10;
  padding: .8rem;
  background: #fff;
  border-radius: 5px;
  text-align: left;
  box-shadow: 0 1px 10px rgba(0, 0, 0, 0.2);
  transition: opacity 0.4s ease, top 0.4s ease, visibility 0.4s ease;
}


.tbl tbody td:last-child .details {
  left: auto;
  right: 0%;
}

.tbl tbody td:hover .details {
  visibility: visible;
  opacity: 1;
  top: calc(100% + 10px);
}
